/*------------------------------------------------------------------
4. Header / #header
-------------------------------------------------------------------*/

#header {
  padding: 35px 0;
  position: relative;
  z-index: 9;
  margin: 0 0 -134px;
}

#header.sticky {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  padding: 20px 0;
  background: #fff;
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
}
#header .hidden-xs {
  display: none !important;
}

