5. logo / .logo
-------------------------------------------------------------------*/

.logo {
  width: 38%;
  float: left;
  margin: 0 10px 0 0;
  padding: 15px 0 0;
}

/*------------------------------------------------------------------
6. nav holder / .nav-holder
-------------------------------------------------------------------*/

.nav-holder {
  float: right;
  width: 122px;
}

.nav-holder .align-right {
  float: right;
  width: 79px;
}

.nav-holder .icon-list {
  font-size: 23px;
  line-height: 25px;
  margin: 0;
  padding: 10px 0 0;
}

.nav-holder .icon-list li {
  float: left;
  margin: 0 0 0 19px;
  position: relative;
}

.nav-holder .icon-list li .cart-num {
  border-radius: 50%;
  width: 15px;
  height: 15px;
  font-size: 10px;
  line-height: 12px;
  color: #fff;
  text-align: center;
  position: absolute;
  bottom: -4px;
  right: -8px;
  z-index: 12;
}

.nav-holder .icon-list li .icon-cart {
  font-size: 22px;
  color: #404040;
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
}

.nav-holder .icon-list li .icon-close {
  position: absolute;
  right: 12px;
  bottom: -1px;
  font-size: 10px;
  color: #404040;
}

.nav-holder .icon-list a {
  text-decoration: none;
}

.nav-holder .cart-drop {
  display: none;
  position: absolute;
  padding: 54px 30px 20px 43px;
  width: 300px;
  top: 59px;
  right: -50px;
  z-index: 999;
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  background: #fff;
  box-shadow: 0 9px 24px 0 rgba(172, 171, 171, 0.75);
}

.nav-holder .cart-drop .main-title {
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  border-style: solid;
  border-width: 0 0 1px;
  border-color: #c1c1c1;
  color: #8a8989;
  margin: 0 0 36px;
  display: block;
  padding: 0 0 3px;
}

.nav-holder .cart-drop .main-title i {
  font-style: normal;
}

.nav-holder .cart-drop .cart-list {
  overflow: hidden;
  padding: 0 0 0 2px;
  margin: 0 0 24px;
  border-width: 0 0 1px;
  border-style: solid;
  border-color: #c1c1c1;
}

.nav-holder .cart-drop .cart-list li {
  margin: 0 0 37px;
  position: relative;
  overflow: hidden;
  float: none;
}

.nav-holder .cart-drop .cart-list li:first-child {
  margin: 0 0 37px;
}

.nav-holder .cart-drop .image {
  float: left;
  width: 68px;
  margin: 0 22px 0 0;
  overflow: hidden;
}

.nav-holder .cart-drop .description {
  overflow: hidden;
}

.nav-holder .cart-drop .product-name {
  font-size: 14px;
  line-height: 16px;
  color: #8a8989;
  margin: 0 0 11px;
  font-weight: normal;
  display: block;
}

.nav-holder .cart-drop .product-name a {
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
  color: #404040;
}

.nav-holder .cart-drop .price {
  font-size: 16px;
  line-height: 18px;
  display: block;
  color: #878484;
}

.nav-holder .cart-drop .total-price-area {
  overflow: hidden;
  padding: 0 9px 0 3px;
  margin: 0 0 29px;
}

.nav-holder .cart-drop .total-price-area .title-text {
  font-size: 14px;
  line-height: 16px;
  color: #8a8989;
  font-weight: 400;
  float: left;
}

.nav-holder .cart-drop .total-price-area .price {
  float: right;
  font-size: 18px;
  line-height: 15px;
  color: #878484;
}

.nav-holder .cart-drop .cart {
  display: block;
  font-size: 16px;
  line-height: 18px;
  color: #878484;
  padding: 0 0 6px 1px;
  border-width: 0 0 1px;
  border-color: #c1c1c1;
  border-style: solid;
  margin: 0 0 51px;
}

.nav-holder .btn-search {
  position: relative;
}

.nav-holder .btn-search .icon-search {
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
  color: #404040;
}

.nav-holder .nav-opener {
  float: left;
  border-radius: 5px;
  border-width: 2px;
  border-style: solid;
  position: relative;
  width: 40px;
  height: 40px;
  padding: 5px;
  text-indent: -9999px;
}

.nav-holder .nav-opener:hover,
.nav-holder .nav-opener:focus {
  outline: none;
}

.nav-holder .nav-opener:hover:before,
.nav-holder .nav-opener:hover:after,
.nav-holder .nav-opener:hover span,
.nav-holder .nav-opener:focus:before,
.nav-holder .nav-opener:focus:after,
.nav-holder .nav-opener:focus span {
  background: #fff;
}

.nav-holder .nav-opener:before,
.nav-holder .nav-opener:after,
.nav-holder .nav-opener span {
  position: absolute;
  content: "";
  left: 10px;
  top: 18px;
  width: 18px;
  height: 3px;
  -webkit-transition: 0.25s linear;
  transition: 0.25s linear;
}

