/*------------------------------------------------------------------
8. Nav / #nav
-------------------------------------------------------------------*/

#nav {
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  width: 100%;
  opacity: 0;
  background: #fff;
  visibility: hidden;
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
}

#nav > ul {
  font-size: 14px;
  line-height: 16px;
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
  -webkit-transform: translateY(-100%);
  -ms-transform: translateY(-100%);
  transform: translateY(-100%);
  position: relative;
  background: #fff;
  color: #676666;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
}

#nav > ul > li {
  margin: 0 0 20px;
  position: relative;
  z-index: 999;
}

#nav > ul > li:hover > .drop,
#nav > ul > li:hover > .drop-holder {
  opacity: 1;
  visibility: visible;
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
  transform: translateY(0);
  display: block;
}

#nav > ul > li > a {
  text-decoration: none;
  text-transform: uppercase;
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
}

#nav > ul > li > .drop {
  opacity: 1;
  visibility: visible;
  position: static;
  width: 100%;
  display: none;
  margin: 10px 0 0;
  padding: 15px 0 9px;
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
  -webkit-transform: translateY(20px);
  -ms-transform: translateY(20px);
  transform: translateY(20px);
  background: #fff;
  box-shadow: 0 9px 24px 0 rgba(172, 171, 171, 0.75);
}

