  margin: 0;
  display: block;
  padding: 10px 16px 10px 23px;
}

#nav > ul > li > .drop > li:hover > .drop-down {
  opacity: 1;
  visibility: visible;
  display: block;
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
  transform: translateY(0);
}

#nav > ul > li > .drop > li > a {
