-------------------------------------------------------------------*/

.btn {
  width: 143px;
  position: relative;
  display: inline-block;
  vertical-align: middle;
  text-decoration: none;
  font-size: 17px;
  line-height: 19px;
  text-transform: uppercase;
  padding: 10px;
  margin: 0 0 20px;
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
}

.btn:before {
  position: absolute;
  content: "";
  z-index: -1;
  left: 0;
  top: 0;
  bottom: 0;
  right: auto;
  border-width: 1px 0 1px 2px;
  border-style: solid;
  z-index: 0;
  padding: 8px;
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
}

.btn:after {
  position: absolute;
  content: "";
  z-index: -1;
  left: auto;
  top: 0;
  bottom: 0;
  right: 0;
  border-width: 1px 2px 1px 0;
  border-style: solid;
  z-index: 0;
  padding: 8px;
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
}

