/*------------------------------------------------------------------
22. Btn Default / .btn-default
-------------------------------------------------------------------*/

.btn-default {
  position: absolute;
  left: 50%;
  top: 50%;
  right: 50%;
  bottom: 50%;
  visibility: hidden;
  opacity: 0;
  height: 80px;
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
  font-size: 24px;
  line-height: 26px;
  color: #676666;
  background: #fff;
  letter-spacing: 5px;
  text-transform: uppercase;
  text-decoration: none;
  width: 248px;
  padding: 29px 15px 23px;
  text-align: center;
  font-weight: bold;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.btn-default:hover {
  text-decoration: none;
  color: #fff;
}

.btn-default:hover:before,
.btn-default:hover:after {
  padding: 42px;
}

.btn-default:before {
  position: absolute;
  content: "";
  z-index: -1;
  left: 0;
  top: -50px;
  bottom: auto;
  right: 0;
  border-width: 6px 6px 0;
  border-style: solid;
  border-color: #fff;
  padding: 12px;
  z-index: 0;
  pointer-events: none;
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
}

.btn-default:after {
  position: absolute;
  content: "";
  z-index: -1;
  left: 0;
  top: auto;
  bottom: -50px;
  right: 0;
  border-width: 0 6px 6px;
  border-style: solid;
  border-color: #fff;
  padding: 12px;
  z-index: 0;
  pointer-events: none;
